
import XHeader from 'vux/src/components/x-header/index.vue'

import showMore from '@/components/ShowMore/showMore'  

export default {
components:{
XHeader,
showMore
},
data(){
return {
resObj: {}
}
},
mounted(){
this.$vux.loading.show({
text: '加载中...'
})
this.initInfo();
},
methods:{
getString(info){
return typeof info !='undefined' ? info : ''
},
initInfo(){

var routerParams = this.$route.params;

this.$ajax.post('',{
'MSH.1':'yesuanService',
'MSH.2':'getById',
'YSID':routerParams.YSID,
'CHZBM':this.$root.loginInfo.CHZBM,
'grdaid':this.$root.loginInfo.grdaid,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$vux.loading.hide()
if(typeof data !='undefined'){
this.resObj = data;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
});

}

},
}



